import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom'
import './App.css';
// Components
import Home from './components/home'

function App() {
      return (
            <BrowserRouter>
                  <Switch>
                        <Route path="/" exact component={ Home } />
                        <Redirect to="/" />
                  </Switch>
            </BrowserRouter>
      );
}

export default App;
