import { useEffect, useState } from 'react';
// AceEditor
import AceEditor from 'react-ace';
import "ace-builds/src-noconflict/ext-language_tools";
// Markdowns
import Markdown from 'markdown-to-jsx';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter'
import { okaidia } from 'react-syntax-highlighter/dist/esm/styles/prism'
// Modules
import Modals from '../../utils/modals';
// Styles
import style from './css/block.module.css';

export default function Block(props) {
      const [desc, setDesc] = useState(props.config.description)
      const [code, setCode] = useState(props.config.code)
      const [descModal, setDescModal] = useState(false)

      // Make Final Changes
      useEffect(() => {
            props.updateBlocks(props.config.id, {
                  id: props.config.id,
                  description: desc,
                  code,
                  language: props.config.language
            })
            // eslint-disable-next-line
      }, [desc, code]);

      const CodeBlock = ({ className, children }) => {
            let lang = 'text'; // default monospaced text
            if (className && className.startsWith('lang-')) {
                  lang = className.replace('lang-', '');
            }
            return (
                  <SyntaxHighlighter language={lang} style={okaidia}>
                        {children}
                  </SyntaxHighlighter>
            );
      }
      const PreBlock = ({ children, ...rest }) => {
            if ('type' in children && children['type'] === 'code') {
                  return CodeBlock(children['props']);
            }
            return <pre style={{ backgroundColor: "#272822" }} {...rest}>{children}</pre>;
      };

      const md_overrides = {
            h1: {
                  component: ({ children, ...props }) => (<h1 {...props}>{children}</h1>),
                  props: { className: style.md_h1 },
            },
            h2: {
                  component: ({ children, ...props }) => (<h2 {...props}>{children}</h2>),
                  props: { className: style.md_h2 },
            },
            h3: {
                  component: ({ children, ...props }) => (<h3 {...props}>{children}</h3>),
                  props: { className: style.md_h3 },
            },
            h4: {
                  component: ({ children, ...props }) => (<h4 {...props}>{children}</h4>),
                  props: { className: style.md_h4 },
            },
            h5: {
                  component: ({ children, ...props }) => (<h5 {...props}>{children}</h5>),
                  props: { className: style.md_h5 },
            },
            h6: {
                  component: ({ children, ...props }) => (<h6 {...props}>{children}</h6>),
                  props: { className: style.md_h6 },
            },
            p: {
                  component: ({ children, ...props }) => (<p {...props}>{children}</p>),
                  props: { className: style.md_p },
            },
            ul: {
                  component: ({ children, ...props }) => (<ul {...props}>{children}</ul>),
                  props: { className: style.md_ul },
            },
            ol: {
                  component: ({ children, ...props }) => (<ol {...props}>{children}</ol>),
                  props: { className: style.md_ul },
            },
            li: {
                  component: ({ children, ...props }) => (<li {...props}>{children}</li>),
                  props: { className: style.md_li },
            },
            a: {
                  component: ({ children, ...props }) => (<a {...props}>{children}</a>),
                  props: { className: style.md_a },
            },
            img: {
                  // eslint-disable-next-line
                  component: ({ children, ...props }) => (<img {...props} />),
                  props: { className: style.md_img },
            },
            pre: PreBlock,
            code: CodeBlock
      }

      return (
            <section className={style.block} id={props.config.id}
                  style={props.selection.selectedBlock === props.config.id ? { borderColor: "#76a4c7" } : {}}
                  onClick={e => { props.selection.setSelectedBlock(props.config.id) }}>
                  <div className={style.head}>
                        <h3 className={style.blockNumber}><a href={`#${props.config.id}`}>{props.pos}.</a></h3>
                        <div className={style.blockDetails}>

                              <div className={style.mdHolder}>
                                    {/* <textarea className={style.description} onChange={e=>{ setDesc(e.target.value) }} placeholder="Description..."></textarea> */}
                                    <div className={style.md}>
                                          <Markdown options={{
                                                forceBlock: true,
                                                wrapper: "div",
                                                forceWrapper: true,
                                                disableParsingRawHTML: true,
                                                overrides: md_overrides
                                          }}>
                                                {desc}
                                          </Markdown>
                                    </div>
                                    <div className={style.mdEditBtn} onClick={e => { setDescModal(true) }}>Edit</div>
                              </div>
                              {(descModal && props.auth) &&
                                    <Modals disableFunc={e => { setDescModal(false) }}>
                                          <div className={style.descModal} onClick={e => { e.stopPropagation() }}>
                                                <div className={style.descModal_div}>
                                                      <h3 className={style.descModal_heading}>Change Description : </h3>
                                                      <p className={style.descModal_p}>* Auto Save Enabled.</p>
                                                      <h3 onClick={e => { setDescModal(false) }} className={style.descModal_close}>⨉</h3>
                                                      <div className={style.descModal_text}>
                                                            {/* <textarea className={style.descModal_textarea}
                                                                  onChange={e => { setDesc(e.target.value); }}
                                                                  value={desc}></textarea> */}
                                                            <AceEditor
                                                                  // name={item}
                                                                  width="100%"
                                                                  height="100%"
                                                                  className={style.descModal_textarea}
                                                                  placeholder="Write about the code..."
                                                                  highlightActiveLine={true}
                                                                  showPrintMargin={true}
                                                                  mode="markdown"
                                                                  theme="idle_fingers"
                                                                  readOnly={!props.auth}
                                                                  value={desc}
                                                                  setOptions={{
                                                                        enableBasicAutocompletion: true,
                                                                        enableLiveAutocompletion: true,
                                                                        enableSnippets: true,
                                                                        showLineNumbers: false,
                                                                        showGutter: false
                                                                  }}
                                                                  onBeforeLoad={ace => {
                                                                        ace.config.set('basePath', '/assets/ace')
                                                                  }}
                                                                  onLoad={editor => {
                                                                        editor.on("showGutterTooltip", tooltip => { tooltip.hide(); });
                                                                  }}
                                                                  onChange={data => {
                                                                        setDesc(data)
                                                                  }} />
                                                      </div>
                                                      <div className={style.descModal_md}>
                                                            <Markdown options={{
                                                                  forceBlock: true,
                                                                  wrapper: "div",
                                                                  forceWrapper: true,
                                                                  disableParsingRawHTML: true,
                                                                  overrides: md_overrides
                                                            }}>
                                                                  {desc}
                                                            </Markdown>
                                                      </div>
                                                </div>
                                          </div>
                                    </Modals>
                              }

                        </div>
                  </div>
                  <AceEditor
                        // name={item}
                        width="100%"
                        height="70px"
                        className={style.editor}
                        style={{ minHeight: "70px" }}
                        placeholder="Welcome to CodeBank..."
                        highlightActiveLine={true}
                        showPrintMargin={true}
                        mode={props.config.language}
                        theme="idle_fingers"
                        readOnly={!props.auth}
                        value={code}
                        setOptions={{
                              enableBasicAutocompletion: true,
                              enableLiveAutocompletion: true,
                              enableSnippets: true
                        }}
                        onBeforeLoad={ace => {
                              ace.config.set('basePath', '/assets/ace')
                        }}
                        onLoad={editor => {
                              editor.on("showGutterTooltip", tooltip => { tooltip.hide(); });
                              editor.on('change', (arg, activeEditor) => {
                                    const aceEditor = activeEditor;
                                    const newHeight = aceEditor.getSession().getScreenLength() *
                                          (aceEditor.renderer.lineHeight + aceEditor.renderer.scrollBar.getWidth());
                                    aceEditor.container.style.height = `${newHeight + 14}px`;
                                    aceEditor.resize();
                              });
                        }}
                        onChange={data => {
                              setCode(data)
                        }} />
            </section>
      )
}
