import { useState } from 'react'
import Modals from '../../utils/modals'
import { ReactComponent as Logo } from '../../assets/Bank.svg'
import style from './css/header.module.css'
import languages from '../../assets/languages'

export default function Header(props) {
      // const [clusterName, setClusterName] = useState(props.clusterName.clusterName)
      const [editClusterName, setEditClusterName] = useState(props.cluster.clusterName)
      const [clusterNameModals, setClusterNameModals] = useState(false)
      // const languages = [ 'Jsx', 'Javascript' ]

      const changeClusterName = () => {
            if (editClusterName === "") {
                  props.cluster.setClusterName("Undefined");
                  setEditClusterName("Unddefined");
            } else {
                  props.cluster.setClusterName(editClusterName);
            }
            setClusterNameModals(false);
      }
      return (
            <header className={style.header}>
                  <div className={style.headHolder}>
                        <div className={style.head}>
                              <Logo className={style.logo} />
                              <h2 className={style.heading}>CodeBank</h2>
                              <h4 className={style.fileName} onClick={e => { setClusterNameModals(true) }} >{props.cluster.clusterName}</h4>
                              {(clusterNameModals && props.auth) &&
                                    <Modals disableFunc={e => { setClusterNameModals(false) }} >
                                          <div className={style.nameModals} onClick={e => { e.stopPropagation(); }} >
                                                <h3 className={style.nameModals_heading}>Rename Cluster : </h3>
                                                <h3 onClick={e => { setClusterNameModals(false) }} className={style.nameModals_close}>⨉</h3>
                                                <div className={style.nameModals_body}>
                                                      <p className={style.nameModals_text}>Enter a new cluster name : </p>
                                                      <input
                                                            className={style.nameModals_input}
                                                            type="text"
                                                            placeholder="Name"
                                                            value={editClusterName}
                                                            onChange={e => { setEditClusterName(e.target.value) }}
                                                            onKeyDown={e => { if (e.key === 'Enter') { changeClusterName() } }} />
                                                      <div onClick={e => { changeClusterName() }} className={style.nameModals_btn}>Rename</div>
                                                      <div onClick={e => { setEditClusterName(props.cluster.clusterName); setClusterNameModals(false) }} className={style.nameModals_btn}>Cancel</div>
                                                </div>
                                          </div>
                                    </Modals>
                              }
                        </div>
                  </div>
                  {props.auth &&
                        <div className={style.toolHolder}>
                              <div className={style.tool}>
                                    <div className={style.tool_btn} onClick={props.tools.addBlock}>Add</div>
                                    <div className={style.tool_btn} style={props.disableTools ? { opacity: '0.5' } : {}} onClick={props.tools.removeBlock}>Delete</div>
                                    <select className={style.tool_btn} defaultValue="javascript" onChange={props.tools.changeLang}>
                                          {languages.map((item, index)=>(
                                                <option value={item.toLowerCase()} key={index} >{item}</option>
                                          ))}
                                    </select>
                              </div>
                        </div>
                  }
            </header>
      )
}
