

export default function Modals(props) {
      const style = {
            overlay: {
                  position: "absolute",
                  width: "100%",
                  height: "100%",
                  top: 0,
                  left: 0,
                  backgroundColor: "#000000ad",
                  zIndex: 100
            },
            body: {
                  position: "relative",
                  zIndex: "2000",
                  top: 0,
                  left: 0,
                  width: "100%",
                  height: "100%",
                  overflow: "auto",
                  display: "flex",
                  justifyContent: "center"
            }
      }

      return (
            <div style={style.overlay} onClick={e => { if (typeof props.disableFunc === 'function') props.disableFunc(); }}>
                  <div style={style.body}>{props.children}</div>
            </div>
      )
}

