import { useState } from "react"
import uniqueString from 'unique-string';
import Header from "./header"
import style from "./css/cluster.module.css"
import Block from "./block"

export default function Cluster(props) {
      const auth = true;
      const [clusterName, setClusterName] = useState("Undefined")
      const f_id = uniqueString();
      const [blocks, setBlocks] = useState({
            [f_id]: {
                  id: f_id,
                  description: "# Heading...\ndescription...",
                  code: "",
                  language: "javascript",
            }
      })
      const [selectedBlock, setSelectedBlock] = useState(f_id)

      const addBlock = e => {
            let id = uniqueString();
            setBlocks({ ...blocks,
                  [id]: {
                        id: id,
                        description: "# Heading...\ndescription...",
                        code: "console.log(\"Hello World\")",
                        language: "javascript",
                  }
            });
      }

      const updateBlocks = (id, data) => {
            setBlocks({ ...blocks, [id]: data })
      }

      const removeBlock = e => {
            let { [selectedBlock]: temp, ...rest } = blocks;
            // console.log(temp);
            setBlocks(rest);
      }

      const changeLang = e => {
            let lang = e.target.value;
            let data = { ...blocks[selectedBlock], language: lang }
            setBlocks({ ...blocks, [selectedBlock]: data })
      }

      return (
            <>
                  <Header cluster={{ clusterName, setClusterName }} tools={{ addBlock, removeBlock, changeLang }} disableTools={selectedBlock?false:true} auth={auth} />
                  <div className={style.body} style={auth?{}:{height: "calc(100% - 41px)"}} >
                        <div className={style.container}>
                              {Object.keys(blocks).map((item, index) => (
                                    <Block key={index} pos={index+1} updateBlocks={updateBlocks} config={blocks[item]} auth={auth} selection={{ selectedBlock, setSelectedBlock }} />
                              ))}
                        </div>
                  </div>
            </>
      )
}
// {/* <Markdown key={item} id={item} data={{ data, setData }} selectedBlocks={{ selectedBlocks, setSelectedBlocks }} /> */}